.home {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
}

.home__header {
  color: white;
  align-self: stretch;
}

.home__header h1 {
  font-style: normal;
  font-weight: normal;
  text-align: center;
  font-size: 24px;
  line-height: 28px;
}

.home__main {
  display: flex;
  flex-direction: column;
  align-content: center;

  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  color: #adadad;
  text-align: center;
}

.home__header-message h1 {
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.home__control {
  margin-bottom: 50px;
}

.home__control-button {
  display: flex;
  background: none;
  justify-content: center;
  cursor: pointer;
}

.questionnaire {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  box-sizing: border-box;
}

.questionnaire__diagram-wrapper {
  display: flex;
  justify-content: center;
}

.questionnaire__diagram {
  color: white;
  font-weight: 700;
  height: 130px;
  width: 130px;
  border-radius: 50%;
  font-size: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  box-sizing: border-box;
  border: 10px solid;
}

.questionnaire__question {
  color: white;
  font-weight: normal;
  font-size: 24px;
  line-height: 23px;
  text-align: center;
}

.questionnaire__options {
  display: flex;
  flex-direction: column;
  margin: 0 30px;
}

.questionnaire__option {
  padding: 16px 20px;
  color: black;
  background-color: white;
  display: flex;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.0416667);
  border-radius: 3px;
  cursor: pointer;
  margin: 16px 0;
  font-size: 20px;
  font-weight: 500;
  transition: 0.1s linear;
  -webkit-transition: 0.1s linear;
  -moz-transition: 0.1s linear;
  -o-transition: 0.1s linear;
}

.questionnaire__option-active {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.questionnaire__option-active:before {
  content: "";
  margin-top: 5px;
  margin-right: 10px;
  width: 16px;
  height: 16px;
  background: url(/vk-mini-app__orbi/static/media/checkicon.710340ba.svg);
}

.questionnaire__control {
  margin-bottom: 50px;
}

.questionnaire__control-button {
  display: flex;
  background: none;
  justify-content: center;
  cursor: pointer;
}

.loading {
  color: white;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0;
}

.loading__header {
  height: 100px;
  align-items: center;
}

.loading__header h1 {
  margin-top: 40px;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.loading__main {
  font-size: 18px;
  line-height: 23px;
  flex: 1 1;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  color: #ADADAD;
}

.loading__main div {
  margin-bottom: 10px;
}

.Panel__in {
  background: -webkit-gradient(linear, left top, left bottom, from(#2b00a5), to(#110041)), #ffffff;
  background: -webkit-linear-gradient(top, #2b00a5 0%, #110041 100%), #ffffff;
  background: linear-gradient(180deg, #2b00a5 0%, #110041 100%), #ffffff;
  height: 100%;
}

.PanelHeader__container,
.PanelHeader-right,
.PanelHeader-left-in,
.PanelHeader-content {
  background-color: #2b00a5;
}

.Group {
  margin: 10px 10px;
  border-radius: 5px;
}

.Group:before,
.Group:after {
  display: none;
}

.main-page {
  color: white;
  display: flex;
  flex-direction: column;
}

.main-page__header {
  display: flex;
  justify-content: space-between;
  -webkit-justify-content: space-between;
  flex-direction: column;
}

.main-page__header-inner {
  padding: 0;
  display: flex;
  justify-content: space-between;
  -webkit-justify-content: space-between;
}

.main-page__header-money {
  font-size: 72px;
  line-height: 86px;
  font-weight: 800;
}

.main-page__header-text {
  padding-top: 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: 800;
}

.main-page__diagram-wrapper {
  display: flex;
  padding-top: 20px;
  justify-content: center;
  -webkit-justify-content: center;
}

.main-page__diagram {
  color: white;
  font-weight: 500;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  -webkit-justify-content: center;
  align-items: center;
  background-color: transparent;
  box-sizing: border-box;
  border: 6px solid;
}

.main-page__diagram-dynamic {
  color: white;
  font-weight: 300;
  font-size: 10px;
  display: flex;
  justify-content: center;
  -webkit-justify-content: center;
  padding: 3px;
}

.main-page__diagram-dynamic-icon {
  margin-top: 2px;
  margin-right: 3px;
}

.main-page__diagram-dynamic-icon-rotated {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.main-page__challenges,
.main-page__recommended {
}

.main-page__challenges-header,
.main-page__recommended-header {
  display: flex;
  justify-content: space-between;
  -webkit-justify-content: space-between;
  align-items: center;
  padding-left: 0;
  padding-right: 0;
}

.main-page__challenges-header-text,
.main-page__recommended-header-text {
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;
}

.main-page__challenges-header-date {
  padding: 8px 12px;
  border-left: 2px solid white;
}

.main-page__challenges-main,
.main-page__recommended-main {
  font-size: 12px;
  line-height: 14px;
  color: #8f8f8f;
  text-align: center;
  padding-top: 0;
}

.main-page__recommended-challenge,
.main-page__challenges-challenge {
  cursor: pointer;
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 45px;
  margin: 10px 0;
  background: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.0416667);
  border-radius: 3px;
}

.main-page__recommended-challenge-title {
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
}

.main-page__recommended-challenge-value div {
  color: #39c4a5;
}

